import React, { useEffect, useState } from 'react';

import p5 from 'p5';
import perro from './assets/perro.png';
import hamburguesa from './assets/hamburguesa.png';
import pizza from './assets/pizza.png';

const Ad = ({ userData, dimensions }) => {
  //const ref = useRef();
  const [url, setUrl] = useState("");
  
  useEffect(()=>{
    const imgSrc = userData.comida==='perro caliente'?perro:
    userData.comida==='hamburguesa'?hamburguesa:
    pizza;
    const sketch = ( s ) => {
      let img;
      s.preload = () => {
        img = s.loadImage(imgSrc);
      }
      s.setup = () => {
        s.noCanvas();
        s.createCanvas(dimensions.w, dimensions.h);
        const graf = s.createGraphics(dimensions.w,dimensions.h).id("graf");
        graf.background(userData.color);
        img.resize(0,dimensions.h);
        graf.imageMode(s.CENTER);
        graf.image(img,dimensions.w/2,dimensions.h/2);
        const title = `¬°Uuuh, ${userData.comida}!`;
        const titleSize = dimensions.w/(title.length/1.7);
        graf.stroke(0).strokeWeight(5).fill(255);
        graf.textAlign(s.CENTER,s.CENTER)
        graf.textSize(titleSize);
        graf.text(title,dimensions.w/2,2*dimensions.h/12);
        const subtitle = "¬øSe te antoja?"
        const subtitleSize = dimensions.w/(subtitle.length)*2;
        graf.textSize(subtitleSize);
        graf.text(subtitle,dimensions.w/2,9*dimensions.h/12);
        const url = document.getElementById('graf').toDataURL('image/png',0.1);
        setUrl(url);
      };
    };
    new p5(sketch);
  },[dimensions,userData])


  return (
    <span className="ad">
      <img src={url} alt="ad"></img>
    </span>
  );
}

export default Ad;