import React, { useState } from "react";

const Tooltip = (props) => {
  let timeout;
  const [active, setActive] = useState(false);
  const showTip = () => {
    setActive(true);
    
  };

  const hideTip = () => {
    clearInterval(timeout);
    timeout = setTimeout(() => {
      setActive(false);
    }, 200);
  };

  return (
    <span
      className="ref-wrapper"
      onMouseEnter={showTip}
      onMouseLeave={hideTip}
    >
      
      {active && (
        <span className="ref-content">
          {props.content}
        </span>
      )}
      {props.children}
    </span>
  );
};

export default Tooltip;