import React, { useState, useRef, useEffect } from 'react';
import * as d3 from 'd3';
import hexToHSL from './hexToHsl';

const Histogram = ({ userData, dimensions }) => {
  let defaultData = [];
  const userCol = hexToHSL(userData.color).h;
  const skew = userCol+180>360?userCol+180-360:userCol+180;
  for (let i=0;i<99;i++) {
    defaultData.push({
      color: randn_bm(skew-180,skew+180),
      user: false
    })
  }
  defaultData.push({color: userCol, user: true});
  const [data] = useState([...defaultData]);
  const ref = useRef();

  useEffect(() => {
    const svg = d3.select(ref.current);
    const margins = {l:60,r:30,t:20,b:60}
    const w = dimensions.w-margins.l-margins.r;
    const h = dimensions.h-margins.t-margins.b;

    svg.append('rect')
      .attr("width",dimensions.w)
      .attr("height",dimensions.h)
      .style("fill", "white");

    const xscale = d3.scaleLinear()
        .domain([0,360])
        .range([0,w])

    svg.append("g")
        .attr("transform",` translate(${margins.l/2},${dimensions.h/2}) rotate(${-90})`)
        .append("text")
        .text("Frecuencia")
        .attr('class','axis-text')
        .attr("x",0)
        .attr("y",0)
        .attr("text-anchor","middle")
        .style("font-size",16)

    svg.append("g")
        .append("text")
        .text("Color preferido (tonalidad)")
        .attr('class','axis-text')
        .attr("x",dimensions.w/2)
        .attr("y",dimensions.h-(margins.b/3))
        .attr("text-anchor","middle")
        .style("font-size",16)

    const histogram = d3.bin()
        .value(d=>+d.color)
        .domain(xscale.domain())
        .thresholds(xscale.ticks(40))

    const bins = histogram(data)

    const yscale = d3.scaleLinear()
        .domain([0,d3.max(bins,d=>d.length)])
        .range([h,0])
        .nice()

    const grad = [];
    for (let i=0;i<1;i+=0.1) {
      grad.push({offset: `${i*100}%`, color: d3.interpolateHslLong("#ff0000", "#ff0001")(i)})
    }

    svg.append("defs")
      .append("linearGradient")
      .attr("id", "gradient")
      .attr("x1", "0%").attr("y1", "0%")
      .attr("x2", "100%").attr("y2", "0%")
      .selectAll("stop")
      .data(grad)
      .enter().append("stop")
      .attr("offset",d=>d.offset)
      .attr("stop-color",d=>d.color);

    svg.append("g")
      .attr("transform",`translate(${margins.l},${h+margins.t})`)
      .append('rect')
      .attr("width",w+1)
      .attr("height",8)
      .style("stroke", "none")
      .style("fill", "url(#gradient)")

    svg.append('g')
      .attr("transform",`translate(${margins.l},${margins.t})`)
      .call(d3.axisLeft(yscale).ticks(20))

    svg.append('g')
      .attr("transform",`translate(${margins.l},${dimensions.h-margins.b})`)
      .call(d3.axisBottom(xscale))
    
    const bars = svg.append("g")
        .attr("transform",`translate(${margins.l},${margins.t})`)
        .selectAll("g")
        .data(bins)
        .enter().append("g")
        .attr("transform",d=>`translate(${xscale(+d.x0)},${yscale(+d.length)})`)
        
    bars.append("rect")
        .attr("width",d=>xscale(d.x1)-xscale(d.x0))
        .attr("height",d=>h-yscale(d.length))
        .attr("rx",0)
        .attr('class',d=>!d.reduce((a,c)=>(a&&c.user),true)?'chart-bar':'chart-bar-highlight')


  },[data,dimensions,userData]);
  
  return (
    <div className="chart">
      <svg className="chart-svg" width={dimensions.w} height={dimensions.h} ref={ref}>
        <defs>
          <linearGradient id="color-gradient" x2="0.35" y2="1">
            <stop offset="0%" stopColor="var(--color-stop)" />
            <stop offset="30%" stopColor="var(--color-stop)" />
            <stop offset="100%" stopColor="var(--color-bot)" />
          </linearGradient>
        </defs>
      </svg>
    </div>
  );
}

function randn_bm(min,max) {
  let u = 0, v = 0;
  while(u === 0) u = Math.random(); //Converting [0,1) to (0,1)
  while(v === 0) v = Math.random();
  let num = Math.sqrt( -2.0 * Math.log( u ) ) * Math.cos( 2.0 * Math.PI * v );

  num = num / 10.0 + 0.5; // Translate to 0 -> 1
  if (num > 1 || num < 0) num = randn_bm(min, max); // resample between 0 and 1 if out of range
  num *= max - min; // Stretch to fill range
  num += min; // offset to min
  return num;
}

export default Histogram;