import React, { useState, useRef, useEffect } from 'react';
import * as d3 from 'd3';

const maxMedias = 40;
const maxFelicidad = 10;
const lineFn = ({a,b}) => x => a + b*x;
const leastSquares = (data,x,y) => {
  const mean = key => data.map(d=>d[key]).reduce((a,c)=>a+c)/data.length;
  const xmean = mean(x);
  const ymean = mean(y);
  const numerator = data.reduce((a,c) => a + ((c[x] - xmean) * (c[y] - ymean)), 0);
  const denominator = data.reduce((a, c) => a + ((c[x] - xmean) * (c[x] - xmean)), 0);
  const b = numerator/denominator;
  const a = ymean - b*xmean;
  return {a,b}
}

const names = ["Mar√≠a","Manuel","Sergio","Paula","Catalina","Andr√©s","Santiago","Alejandra","Fernanda","Jorge"];
const defaultData = names.map(name=>{
  const medias = Math.floor(Math.random() * maxMedias);
  const felicidad = d3.min([10,(medias/(maxMedias/10)) + ((Math.random() * 2))]);
  return {medias,felicidad,user:false,name};
});

let l = lineFn(leastSquares(defaultData,'medias','felicidad'));

const Regression = ({ userData, dimensions }) => {
  const ref = useRef();
  const [data, setData] = useState([...defaultData,{
    medias: userData.medias,
    felicidad: l(userData.medias),
    user: true,
    name: "T√∫"
  }]);
  useEffect(() => {
    const svg = d3.select(ref.current);
    const margins = {l:60,r:30,t:20,b:60}
    const w = dimensions.w-margins.l-margins.r;
    const h = dimensions.h-margins.t-margins.b;
    svg.append('rect')
      .attr("width",dimensions.w)
      .attr("height",dimensions.h)
      .attr("fill","white")

    const yscale = d3.scaleLinear()
        .domain([0,maxFelicidad])
        .range([h,0])

    const iyscale = d3.scaleLinear()
        .domain([h,0])
        .range([0,maxFelicidad])

    const xscale = d3.scaleLinear()
        .domain([0,maxMedias])
        .range([0,w])

    const ixscale = d3.scaleLinear()
        .domain([0,w])
        .range([0,maxMedias])
    
    svg.append("g")
        .attr("transform",` translate(${margins.l/2},${dimensions.h/2}) rotate(${-90})`)
        .append("text")
        .text("Felicidad")
        .attr('class','axis-text')
        .attr("x",0)
        .attr("y",0)
        .attr("text-anchor","middle")
        .style("font-size",16)

    svg.append("g")
        .append("text")
        .text("N√∫mero de pares de medias")
        .attr('class','axis-text')
        .attr("x",dimensions.w/2)
        .attr("y",dimensions.h-(margins.b/3))
        .attr("text-anchor","middle")
        .style("font-size",16)

    svg.append('g')
        .attr("transform",()=>`translate(${margins.l},${margins.t})`)
        .call(d3.axisLeft(yscale).ticks(10))
    
    svg.append('g')
        .attr("transform",()=>`translate(${margins.l},${dimensions.h-margins.b})`)
        .call(d3.axisBottom(xscale))

    svg.on("click",function(e) {
      const newx = Math.round(ixscale(d3.pointer(e)[0]-margins.l));
      const newy = +iyscale(d3.pointer(e)[1]-margins.t).toFixed(1);
      const newData = [...data,{
        medias: newx,
        felicidad: newy,
        user: false,
        name: ""
      }]
      l = lineFn(leastSquares(newData,'medias','felicidad'));
      setData([...newData]);
    })

    const linePoints = [{
      x1: xscale(0),
      y1: yscale(l(0)),
      x2: xscale(maxMedias),
      y2: yscale(l(maxMedias))
    }]
    
    svg.append('g')
      .attr("transform",()=>`translate(${margins.l},${margins.t})`)
      .selectAll('line')
      .data(linePoints)
      .join('line')
      .attr("class","chart-line")
      .attr('x1',d=>d.x1)
      .attr('y1',d=>d.y1)
      .attr('x2',d=>d.x2)
      .attr('y2',d=>d.y2);

    const dots = svg.append('g')
      .attr("transform",()=>`translate(${margins.l},${margins.t})`)
      .selectAll('circle')
      .data(data)
      .join('circle')
      .attr('class',d=>d.user?'chart-dot-highlight':'chart-dot')
      .attr('r',6)
      .attr('cx',d=>xscale(d.medias))
      .attr('cy',d=>yscale(d.felicidad))
      .style('cursor','pointer');

    dots.on("mouseover", function(e,d) {
        let g = svg.append("g")
          .classed("chart-tooltip", true)
          .attr("transform", `translate(${xscale(d.medias)},${yscale(d.felicidad)+25})`)
  
        g.append("rect")
          .attr("width", 110)
          .attr("height", 60)
          .attr("rx", 4)
          .style("fill", "white")
          .style("stroke", "black")

        g.append("text")
          .attr("y", 20)
          .attr("x", 5)
          .text(d.name)
  
        g.append("text")
          .attr("y", 35)
          .attr("x", 5)
          .text("Pares Medias: "+d.medias)
  
        g.append("text")
          .attr("y", 50)
          .attr("x", 5)
          .text("Felicidad: "+(d.felicidad+"").substring(0,4))
      })
      .on("mouseout", function (d) {
        svg.selectAll(".chart-tooltip").remove()
      })
  },[data,dimensions]);

  const faceValue = l(userData.medias)<4?"üòî"
                   :l(userData.medias)>=4&&l(userData.medias)<6?"üòê"
                   :l(userData.medias)>=6&&l(userData.medias)<9?"üôÇ"
                   :l(userData.medias)>=9?"üòÄ"
                   :""
  
  return (
    <div className="chart">
      <svg className="chart-svg" width={dimensions.w} height={dimensions.h} ref={ref}></svg>
  <p className="content-paragraph">La predicci√≥n de tu nivel felicidad de acuerdo con tus pares de medias es: {(l(userData.medias)+"").substring(0,6)} {faceValue}</p>
    </div>
  );
}

export default Regression;