import React, { useState, useRef, useEffect } from 'react';
import * as d3 from 'd3';
import * as ml5 from 'ml5';
import hexToHSL from './hexToHsl';

const NearestN = ({ userData, dimensions }) => {
  const color = hexToHSL(userData.color).h;
  let defaultData = [];
  const bases = [
    {x:color,y:userData.edad},
    {x:color+120>360?color+120-360:color+120,y:userData.edad+15},
    {x:color+240>360?color+240-360:color+240,y:userData.edad+30}
  ]
  bases.map(b=>{
    for (let i=0;i<30;i++) {
      const tempx = b.x+((Math.floor(Math.random() * 120))-60);
      const x = tempx>360?tempx-360:tempx<0?tempx+360:tempx;
      const y = b.y+((Math.floor(Math.random() * 20))-10);
      defaultData.push({x,y,user:false});
    }
    return true
  })

  defaultData.push({x:color,y:userData.edad,user:true})
  
  const [data, setData] = useState([...defaultData]);
  const cluster = () => {
    const normalData = data.map(d=>({x:d.x/360,y:d.y/Math.max(...data.map(e=>e.y))}));
    const kmeans = ml5.kmeans(normalData,{k:3},()=>{
      const clustering = data.map((d,i)=>{
        const c = kmeans.dataset[i].centroid
        const cat = c===0?'bueno':c===1?'regular':'malo';
        return {...d, cluster: c, cat}
      });
      setData([...clustering]);
    });
  }
  
  const ref = useRef();

  useEffect(() => {
    const svg = d3.select(ref.current);
    const margins = {l:60,r:30,t:20,b:60}
    const w = dimensions.w-margins.l-margins.r;
    const h = dimensions.h-margins.t-margins.b;

    svg.append('rect')
      .attr("width",dimensions.w)
      .attr("height",dimensions.h)
      .attr("fill","white")
    
    let yscale = d3.scaleLinear()
        .domain(d3.extent(data.map(d=>d.y)))
        .range([h,0])
        .nice()

    let xscale = d3.scaleLinear()
        .domain([0,360])
        .range([0,w])
    
    svg.append("g")
        .attr("transform",` translate(${margins.l/2},${dimensions.h/2}) rotate(${-90})`)
        .append("text")
        .text("Edad")
        .attr('class','axis-text')
        .attr("x",0)
        .attr("y",0)
        .attr("text-anchor","middle")
        .style("font-size",16)

    svg.append("g")
        .append("text")
        .text("Color preferido (tonalidad)")
        .attr('class','axis-text')
        .attr("x",dimensions.w/2)
        .attr("y",dimensions.h-(margins.b/3))
        .attr("text-anchor","middle")
        .style("font-size",16)

    const grad = [];
    for (let i=0;i<1;i+=0.1) {
      grad.push({offset: `${i*100}%`, color: d3.interpolateHslLong("#ff0000", "#ff0001")(i)})
    }

    svg.append("defs")
      .append("linearGradient")
      .attr("id", "gradient")
      .attr("x1", "0%").attr("y1", "0%")
      .attr("x2", "100%").attr("y2", "0%")
      .selectAll("stop")
      .data(grad)
      .enter().append("stop")
      .attr("offset",d=>d.offset)
      .attr("stop-color",d=>d.color);

    svg.append("g")
      .attr("transform",`translate(${margins.l},${h+margins.t})`)
      .append('rect')
      .attr("width",w+1)
      .attr("height",8)
      .style("stroke", "none")
      .style("fill", "url(#gradient)")

    svg.append('g')
        .attr("transform",()=>`translate(${margins.l},${margins.t})`)
        .call(d3.axisLeft(yscale).ticks(10))
    
    svg.append('g')
        .attr("transform",()=>`translate(${margins.l},${dimensions.h-margins.b})`)
        .call(d3.axisBottom(xscale))

    const dots = svg.append('g')
      .attr("transform",()=>`translate(${margins.l},${margins.t})`)
      .selectAll('circle')
      .data(data)
      .enter().append('circle')
      .attr('class',d=>d.user?'chart-cat-highlight':'chart-cat')
      .attr('r',6)
      .attr('cx',d=>xscale(d.x))
      .attr('cy',d=>yscale(d.y))
      .style('fill',(d=>{
        if (d.cluster===0) {
          return 'cyan'
        } else if (d.cluster===1) {
          return 'magenta'
        } else if (d.cluster===2) {
          return 'yellow'
        }
      }))
      .style('cursor','pointer');

    dots.on("mouseover", function(e,d) {
        let g = svg.append("g")
          .classed("chart-tooltip", true)
          .attr("transform", `translate(${xscale(d.x)},${yscale(d.y)+25})`)
  
        g.append("rect")
          .attr("width", 135)
          .attr("height", 60)
          .attr("rx", 4)
          .style("fill", "white")
          .style("stroke", "black")

        g.append("text")
          .attr("y", 20)
          .attr("x", 5)
          .text("Categor√≠a: "+d.cat)
  
        g.append("text")
          .attr("y", 35)
          .attr("x", 5)
          .text("Edad: "+d.y)
  
        g.append("text")
          .attr("y", 50)
          .attr("x", 5)
          .text("Color: "+d.x)
      })
      .on("mouseout", function (d) {
        svg.selectAll(".chart-tooltip").remove()
      })
  },[data,dimensions,userData]);
  
  return (
    <div className="chart">
      <svg className="chart-svg" width={dimensions.w} height={dimensions.h} ref={ref}></svg>
      <div>
        <span className="content-paragraph">Haz clic en el bot√≥n para categorizar: </span>
        <button onClick={()=>{cluster()}} className="default-button">Agrupar</button>
      </div>
    </div>
  );
}

export default NearestN;